.hero-image {
  width: 100%;
  height: 100%;
  position: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  opacity: .5;
}

@media only screen and (max-width: 768px) {
  /* .hero-video iframe, .hero-video{
    display: none;
  } */

  .header-text {
    font-size: .75em;
  }


  .header-text h3 {
    margin-top: -15px;
    font-size: .75em;
    letter-spacing: 1.5px;
  }

  .scroll-indicator {
    -webkit-animation: fade-in-e 10s ease;
    animation: fade-in-e 10s ease;
